import { useState } from "react";
import { Gallery } from "react-grid-gallery";
import Lightbox from "react-image-lightbox";
import axios from 'axios'
import "react-image-lightbox/style.css";
import './index.css'

export default function App() {
  const [index, setIndex] = useState(-1);
  const [images, setImages] = useState([]);
  const [processing, setProcessing] = useState(false);
  const [processed, setProcessed] = useState(false);

  const currentImage = images[index];
  const nextIndex = (index + 1) % images.length;
  const nextImage = images[nextIndex] || currentImage;
  const prevIndex = (index + images.length - 1) % images.length;
  const prevImage = images[prevIndex] || currentImage;

  const handleClick = (index, item) => setIndex(index);
  const handleClose = () => setIndex(-1);
  const handleMovePrev = () => setIndex(prevIndex);
  const handleMoveNext = () => setIndex(nextIndex);



const getImgData = (ev) => {
  const files = ev.target.files;
  const imagesData = []

  for(let i=0; i<files.length; i++) {

    const file = files[i]
    const fileReader = new FileReader();
    fileReader.readAsDataURL(file);

    fileReader.onload = function (e) {
      //Initiate the JavaScript Image object.
      const image = new Image();

      //Set the Base64 string return from FileReader as source.
      image.src = e.target.result;

      //Validate the File Height and Width.
      image.onload = function () {
        const height = this.height;
        const width = this.width;
        imagesData.push({ height, width, src: image.src, light: width > height })

        if(i === files.length - 1) {
          setImages(imagesData)
        }
        return true;
      };
      };
  }
}

const processedImagesHandler = async() => {
  if(processed) {
    setImages([])
    setProcessed(false)
    return;
  }

  setProcessing(true)
  axios.post('http://ala.freshandfunky.co/process', { images })
  .then(res => {
    setImages(res.data)
    setProcessing(false)
    setProcessed(true)
  })
  .catch(err =>  {
    console.log(err)
    setProcessing(false)
  })
}

const downloadImagesHandler = async() => {
  return axios({
    url: 'http://ala.freshandfunky.co/download',
    method: 'GET',
    responseType: 'blob',
  })
    .then(response => {
      const href = window.URL.createObjectURL(response.data);

      const anchorElement = document.createElement('a');

      anchorElement.href = href;
      anchorElement.download = 'processedImages.zip';

      document.body.appendChild(anchorElement);
      anchorElement.click();

      document.body.removeChild(anchorElement);
      window.URL.revokeObjectURL(href);
    })
    .catch(error => {
      console.log('error: ', error);
    });
}

  return (
    <div className='wrapper'>
      {images.length === 0 && <input type='file' id="fileInput"  accept="image/*"
      onChange={getImgData} multiple />}
      { images.length !== 0 && <>
      <div className='buttonWrapper'>
        <button className="button-41" onClick={processedImagesHandler} disabled={processing}>
          {processing ? 'Processing...' : processed ? 'Restart' : 'Process Images'}
        </button> 
        <br/>
       {processed && <button className="button-41" onClick={downloadImagesHandler} disabled={processing}>
          Download All
        </button>
        }
      </div>
      <Gallery
        images={images}
        onClick={handleClick}
        enableImageSelection={false}
      />
      {!!currentImage && (
        <Lightbox
          mainSrc={currentImage.src}
          imageTitle={currentImage.caption}
          mainSrcThumbnail={currentImage.src}
          nextSrc={nextImage.src}
          nextSrcThumbnail={nextImage.src}
          prevSrc={prevImage.src}
          prevSrcThumbnail={prevImage.src}
          onCloseRequest={handleClose}
          onMovePrevRequest={handleMovePrev}
          onMoveNextRequest={handleMoveNext}
        />
      )}
      </> }
      
    </div>
  );
}